import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import DefaultHD from './default';
import { notification, Spin } from 'antd';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    paper: {
        marginTop: theme.spacing(0),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
});



class SignUp extends React.Component {

    componentWillReceiveProps = (prop) => {
        if(prop.token !== null)
        {
            this.props.history.push("/stock-market")
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        const data = new FormData(e.target)
        this.props.signUp(data.get('username'), data.get('email'), data.get('password1'), data.get('password2'));
    }
    render() {
        const { classes } = this.props;
        let stringerr = "Sign up failed. Please try again !";
        if (this.props.error) {
            if (this.props.error.response.data)
            {
                var dataerr = this.props.error.response.data;
                var allerr = Object.keys(dataerr)
                if(allerr.length > 0)
                {
                    stringerr = dataerr[allerr[0]][0]
                }
            } 
            notification["error"]({
                message: 'Error',
                description: stringerr,
                duration: 6,
            });
        }
        return (
            <Container component="main" maxWidth="xs">
                <DefaultHD />
                <CssBaseline />
                <div className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up
                    </Typography>

                    <Spin spinning={this.props.loading}>
                        <form className={classes.form} onSubmit={(e) => this.handleSubmit(e)}>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <TextField autoComplete="none" name="username" variant="outlined" required
                                        fullWidth id="username" label="UserName" autoFocus />
                                </Grid>
                                
                                <Grid item xs={12}>
                                    <TextField variant="outlined" required fullWidth id="email"
                                        label="Email Address" name="email" autoComplete="none" />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField variant="outlined" required fullWidth name="password1" label="Password"
                                        type="password" id="password1" autoComplete="none" />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField variant="outlined" required fullWidth name="password2" label="Confirm password"
                                        type="password" id="password2" autoComplete="none" />
                                </Grid>
                            </Grid>
                            <Button type="submit" fullWidth variant="contained"
                                color="primary" className={classes.submit} >
                                Sign Up
                            </Button>
                            <Grid container justify="flex-end">
                                <Grid item>
                                    <Link to="/login" style={{ variant : "body2"}}>
                                        Already have an account? Sign in
                                    </Link>
                                </Grid>
                            </Grid>
                        </form>
                    </Spin>
                </div>
                <Box mt={5}>
                    <Copyright />
                </Box>
            </Container>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loading: state.loading,
        error: state.error,
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        signUp: (username, email, password1, password2) => dispatch(actions.authSignup(username, email, password1, password2)) 
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(SignUp));