import React from 'react';
import { List} from 'antd';
import PropTypes from 'prop-types';
import { Button } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';


export default function HistoryDownload (props) {
    const { listlink } = props;

    return (
        <List itemLayout="vertical" size="large" style={{ minHeight: "58vh" }}
            pagination={{
                onChange: page => {
                    console.log(page);
                },
                pageSize: 10,
            }} dataSource={listlink}
            renderItem={item => (
                <List.Item key={item.linkget}
                    extra={
                        <Button type="primary" icon={<DownloadOutlined />} size="large" onClick={() => {window.open(item.linkget)}}>
                            Download
                        </Button>
                    } >
                    <List.Item.Meta style={{width: "80vh"}}
                        title={item.linkget}
                    />
                </List.Item>
            )}/>
    )
}

HistoryDownload.propTypes = {
    listlink: PropTypes.object,
};