import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import {Link} from 'react-router-dom';
import Box from '@material-ui/core/Box';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import InputIcon from '@material-ui/icons/Input';
import { Spin, Popover, Menu, Dropdown } from 'antd';
import Toolbar from '@material-ui/core/Toolbar';
import HomeIcon from '@material-ui/icons/Home';
import { UserOutlined, MoneyCollectOutlined, GiftOutlined, FieldTimeOutlined } from '@ant-design/icons';
import axios from 'axios';
import HistoryDownload from './history';
import * as api from '../../api';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: '60vw',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
    },
    toolbarLink: {
        padding: theme.spacing(1),
        flexShrink: 0,
    },
});

class Logout extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            getting: false,
            link: "",
            user: {}
        }
    }

    getuserprofile() {
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_user_detail,{})
        .then(resp => {
            console.log(resp.data)
            this.setState({user:resp.data})
        })
        .catch(err => {
            console.log(err)
        })
    }

    componentDidMount() {
        this.getuserprofile();
    }

    render() {
        const { classes } = this.props;
        
        return (
            <Spin spinning={!this.state.user.username}>
            <Container component="main" maxWidth="lg" style={{overflow: "hidden"}}>
                <React.Fragment>
                    <Toolbar className={classes.toolbar}>
                        <Popover content="Home"> 
                            <IconButton>
                                <Link to="/">
                                    <HomeIcon />
                                </Link>
                            </IconButton>
                        </Popover>
                        <Typography component="h2" variant="h5"
                                    color="inherit" align="center"
                                    noWrap className={classes.toolbarTitle}>
                            Get link download premium stock
                        </Typography>
                        <Dropdown.Button overlay={(
                            <Menu>
                                <Menu.Item key="1" icon={<MoneyCollectOutlined />}>
                                    Money: {this.state.user.money} (VNĐ)
                                </Menu.Item>
                                <Menu.Item key="2" icon={<GiftOutlined />}>
                                    Package: {this.state.user.packagename}
                                </Menu.Item>
                                <Menu.Item key="3" icon={<FieldTimeOutlined />}>
                                    Expire: {
                                        this.state.user.packagetime === 0 ?
                                        "Vô thời hạn"
                                        :
                                        new Date(this.state.user.packagetime*1000).toUTCString().slice(4,16)
                                    }
                                </Menu.Item>
                            </Menu>
                        )} placement="bottomCenter" icon={<UserOutlined />} trigger="click">
                            {this.state.user.username}
                        </Dropdown.Button>
                        <Popover content="Logout">
                            <IconButton onClick={() => this.props.logout()}>
                                <ExitToAppIcon />
                            </IconButton>
                        </Popover>
                    </Toolbar>
                </React.Fragment>
                
                <CssBaseline />
                <div className={classes.paper}>
                    <Spin spinning={this.state.getting}>
                        <Paper component="form" className={classes.root}>
                            <IconButton className={classes.iconButton} aria-label="menu">
                                <InputIcon />
                            </IconButton>
                            <InputBase
                                className={classes.input}
                                placeholder="Link stock: https://www.freepik.com/premium-psd/something-you-need_123456.htm"
                                onChange={(e) => this.setState({link: e.target.value})}
                            />
                            <Divider className={classes.divider} orientation="vertical" />
                            <Popover content="Get link download">
                                <IconButton color="primary" className={classes.iconButton} aria-label="directions" onClick={() => this.setState({getting: true})} >
                                    <CloudDownloadIcon />
                                </IconButton>
                            </Popover>
                        </Paper>
                    </Spin>
                </div>
<HistoryDownload listlink={this.state.user.history} />
                <Box mt={8}>
                    <Copyright />
                </Box>
            </Container>
            </Spin>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(actions.logout())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Logout));