import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import {Link} from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import DefaultHD from './default';
import { notification, Spin } from 'antd';


function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    paper: {
        marginTop: theme.spacing(0),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
});



class Login extends React.Component {
    
    constructor(props) {
        super(props);
    }

    handleSubmit(e) {
        e.preventDefault();
        const data = new FormData(e.target)
        this.props.onAuth(data.get('username'), data.get('password'));
    }

    componentWillReceiveProps = (prop) => {
        if(prop.token !== null)
        {
            this.props.history.push("/stock-market")
        }
    }

    render() {
        const { classes } = this.props;
        let stringerr = "Login failed. Please try again !";
        if (this.props.error) {
            if (this.props.error.response.data)
            {
                var dataerr = this.props.error.response.data;
                var allerr = Object.keys(dataerr)
                if(allerr.length > 0)
                {
                    stringerr = dataerr[allerr[0]][0]
                }
            } 
            notification["error"]({
                message: 'Error',
                description: stringerr,
                duration: 6,
            });
        }

        return (
            <Container component="main" maxWidth="xs" style={{overflow: "hidden"}}>
                <DefaultHD />
                <CssBaseline />
                <div className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign in
                    </Typography>
                    <Spin spinning={this.props.loading}>
                        <form className={classes.form} noValidate onSubmit={(e) => this.handleSubmit(e)}>
                            <TextField variant="outlined" margin="normal" required fullWidth required
                                id="username" label="Username" name="username" autoComplete="none" autoFocus />
                            <TextField variant="outlined" margin="normal" required fullWidth required name="password" 
                                label="Password" type="password" id="password" autoComplete="current-password" />
                            <Button type="submit" fullWidth variant="contained" 
                                color="primary" className={classes.submit} >
                                Sign In
                            </Button>
                            <Grid container>
                                <Grid item xs>
                                
                                </Grid>
                                <Grid item>
                                    <Link to="/signup">
                                        {"Don't have an account? Sign Up"}
                                    </Link>
                                </Grid>
                            </Grid>
                        </form>
                    </Spin>
                </div>
                <Box mt={8}>
                    <Copyright />
                </Box>
            </Container>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loading: state.loading,
        error: state.error,
        token: state.token
    }

}

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (username, password) => dispatch(actions.authLogin(username, password)) 
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Login));