import React from 'react';
import SignUp from './signup';
import Logout from './logout';

class MainStock2 extends React.Component {

    render() {
        return (
            <React.Fragment>
                {
                    this.props.isAuthenticated ?
                    <Logout />
                    :
                    <SignUp />
                }
            </React.Fragment>
        )
    }
}

export default MainStock2;