import React, { Component } from 'react';
import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'
import { HashRouter as Router } from 'react-router-dom';
import { Route, Switch } from 'react-router-dom';
import Blog from './pages/blog/Blog';
import PostDetail from './pages/blog/PostDetail';
import './App.css';
import HomeStock from './pages/stock/homestock';
import HomeStock2 from './pages/stock/homestock2';

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route path="/login" exact component={HomeStock} />
          <Route path="/signup" exact component={HomeStock2} />
          <Route path="/stock-market" exact component={HomeStock} />
          <Route path="/post/:postid" exact component={PostDetail} />
          <Route path="*" component={Blog} />
        </Switch>
      </Router>
    )
  }
}

export default App;
