import React from 'react';
import Login from './login';
import Logout from './logout';
class MainStock extends React.Component {

    render() {
        return (
            <React.Fragment>
                {
                    this.props.isAuthenticated ?
                    <Logout />
                    :
                    <Login />
                }
            </React.Fragment>
        )
    }
}

export default MainStock;